import React, { useContext }  from 'react';
import { navigate } from "@reach/router";
import AppContext from '../../contextAPI/AppContext'
import clsx from 'clsx';
import Select from 'react-select';
import { emphasize, makeStyles, useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import NoSsr from '@material-ui/core/NoSsr';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import MenuItem from '@material-ui/core/MenuItem';
import CancelIcon from '@material-ui/icons/Cancel';
import PropTypes from 'prop-types';
import KeyPassages from '../../data/KeyPassages.json'
import AvailableContent from '../../data/availableContent.json'
import GetSegment from '../../scripts/NavigateToSegment'
import scrollToElement from 'scroll-to-element'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 'auto',
  },
  input: {
    display: 'flex',
    padding: 0,
    height: 'auto',
  },
  valueContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    flex: 1,
    alignItems: 'center',
    overflow: 'hidden',
  },
  chip: {
    margin: theme.spacing(0.5, 0.25),
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === 'light' ? theme.palette.grey[300] : theme.palette.grey[700],
      0.08,
    ),
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2),
  },
  singleValue: {
    fontSize: 16,
  },
  placeholder: {
    position: 'absolute',
    left: 2,
    bottom: 6,
    fontSize: 16,
  },
  paper: {
    position: 'absolute',
    zIndex: 1,
    marginTop: theme.spacing(1),
    left: 0,
    right: 0,
  },
  divider: {
    height: theme.spacing(2),
  },
}));

function NoOptionsMessage(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.noOptionsMessage}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

NoOptionsMessage.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />;
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
};

function Control(props) {
  const {
    children,
    innerProps,
    innerRef,
    selectProps: { classes, TextFieldProps },
  } = props;

  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: classes.input,
          ref: innerRef,
          children,
          ...innerProps,
        },
      }}
      {...TextFieldProps}
    />
  );
}

Control.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
  selectProps: PropTypes.object.isRequired,
};

function Option(props) {
  return (
    <MenuItem
      ref={props.innerRef}
      selected={props.isFocused}
      component="div"
      style={{
        fontWeight: props.isSelected ? 500 : 400,
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  );
}

Option.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
  isFocused: PropTypes.bool,
  isSelected: PropTypes.bool,
};

function Placeholder(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.placeholder}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

Placeholder.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function SingleValue(props) {
  return (
    <Typography className={props.selectProps.classes.singleValue} {...props.innerProps}>
      {props.children}
    </Typography>
  );
}

SingleValue.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function ValueContainer(props) {
  return <div className={props.selectProps.classes.valueContainer}>{props.children}</div>;
}

ValueContainer.propTypes = {
  children: PropTypes.node,
  selectProps: PropTypes.object.isRequired,
};

function MultiValue(props) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={clsx(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused,
      })}
      onDelete={props.removeProps.onClick}
      deleteIcon={<CancelIcon {...props.removeProps} />}
    />
  );
}

MultiValue.propTypes = {
  children: PropTypes.node,
  isFocused: PropTypes.bool,
  removeProps: PropTypes.object.isRequired,
  selectProps: PropTypes.object.isRequired,
};

function Menu(props) {
  return (
    <Paper square className={props.selectProps.classes.paper} {...props.innerProps}>
      {props.children}
    </Paper>
  );
}

Menu.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object,
};

const components = {
  Control,
  Menu,
  MultiValue,
  NoOptionsMessage,
  Option,
  Placeholder,
  SingleValue,
  ValueContainer,
};

export default function IntegrationReactSelect(props) {
  const receivedAppContext = useContext(AppContext)
  const classes = useStyles();
  const theme = useTheme();
  let currentKPObj = {
    label: '',
    value: ''
  }
  const filteredBySphere = Object.values(KeyPassages).filter(k => k.spheres[0] === receivedAppContext.currentSphere)
  console.log(filteredBySphere)
  const filteredByNumber = filteredBySphere.filter(p => p.number === receivedAppContext.currentKeyPassage)
  console.log(filteredByNumber)
  if (filteredByNumber.length !== 0) {
    currentKPObj = {
    label: `${filteredByNumber[0].spheres[0]} #${filteredByNumber[0].number}: ${filteredByNumber[0].title}`,
    value: filteredByNumber[0].number
  }}

  const getKeyPassages = (sphere) => {
    const KPData = Object.values(KeyPassages).filter(p => p.spheres[0] === sphere)
    const options = KPData.map(kp => {
      return {
        label: `${kp.spheres[0]} #${kp.number}: ${kp.title}`,
        value: kp.number
      }
    })
    return options
  }

  const [single, setSingle] = React.useState(currentKPObj);
  if (single === null) {
    setSingle({
      label: '',
      value: ''
    });
  }

  console.log(receivedAppContext.currentKeyPassage, single)
  const search = (RefID) => {
    const refInfo = RefID.split('-')
    console.log(RefID, refInfo)
    const segment = GetSegment(refInfo[0], parseInt(refInfo[1]), parseInt(refInfo[2]))
    receivedAppContext.updateDisplayRef(true)
    receivedAppContext.updateSearchID(RefID)
    if (receivedAppContext.currentSegmentID === segment) {
      props.closeMenu()
      samePageNavigation(`#${RefID}`)
    } else {
      props.closeMenu()
      navigate(`/${receivedAppContext.language}/${receivedAppContext.version}/${segment}`,
        { state: { currentInterlinear: receivedAppContext.interlinear, currentShowSpheres: receivedAppContext.showSpheres }})
    }
  }

  const samePageNavigation = (id) => {
    scrollToElement(id, {
        offset: -65,
        ease: 'inOutCube',
        duration: 1500
    });
    receivedAppContext.updateSearchID('')
  }

  function handleChangeSingle(value) {
    setSingle(value);
    receivedAppContext.updateKeyPassage(value.value);
    search(KeyPassages[`${receivedAppContext.currentSphere}-${value.value}`].ref)
  }

  const selectStyles = {
    input: base => ({
      ...base,
      color: theme.palette.text.primary,
      '& input': {
        font: 'inherit',
      },
    }),
  };

  console.log(receivedAppContext)

  return (
    <div className={classes.root}>
      <NoSsr>
        <Select
          classes={classes}
          styles={selectStyles}
          inputId="react-select-single"
          TextFieldProps={{
            label: 'Key Passage',
            InputLabelProps: {
              htmlFor: 'react-select-single',
              shrink: true,
            },
            placeholder: 'Search Key Passages',
          }}
          options={getKeyPassages(receivedAppContext.currentSphere)}
          components={components}
          value={single}
          onChange={handleChangeSingle}
        />
      </NoSsr>
    </div>
  );
} 